.resume_wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    background-color: #ffffff;
}
.resume_body {
    max-width: 800px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-template-rows: 2fr 3fr 3fr;
    /*grid-gap: 4px;*/
    border: 1px solid black;
}
.resume_body > * {
    /*border: 1px solid black;*/
    margin: 5px;
}
.resume_body img:hover {
    transition-duration: 0.2s;
    transform: scale(1.1);
}
.name_block {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}
.name_block * {
    padding-left: 20px;
}
.contacts_block {
    display: grid;
    grid-template-columns: 2fr 3fr;
    grid-gap: 10px;
    height: 110px;
}
.basic_contacts {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    padding: 0 7px;
    max-height: 140px;
}
.basic_contacts > div {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-height: min-content;
}
.basic_contacts > div > img {
    max-width: 30px;
    max-height: 30px;
}
.basic_contacts > div > p {
    box-shadow: 0 5px 5px -5px rgba(34, 60, 80, 0.6);
}

.links_block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-height: 150px;
    background-color: #ffffff;
    /*border: 1px solid gray;*/
    box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
    border-radius: 5px;
}
.links_block p {
    text-align: center;
    font-size: large;
    font-weight: bold;
    box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
    max-width: max-content;
    padding: 8px;
    border-radius: 8px;
}
.links {
    display: flex;
}
.links a {
    margin: 5px;
    max-width: 40px;
    max-height: 40px;
}
.links img {
    max-width: 100%;
    max-height: 100%;
}

.hard_skills {
    display: flex;
    /*flex-direction: column;*/
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: start;
    /*display: grid;*/
    /*grid-template-columns: 1fr 1fr 1fr;*/
    /*grid-template-rows: 1fr 1fr 1fr;*/
    max-height: 230px;
}
.hard_skills p {
    width: max-content;
    height: max-content;
    line-height: 100%;
    vertical-align: center;
    box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);
    padding: 10px 3px;
    border-radius: 10px;
    margin: 5px;
    color: #422020;
}
.hard_skills span:hover {
    box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2) inset;
    /*cursor: pointer;*/
}
