
.not_found_page {
    position: relative;
    width: 100%;
}
.not_found_page button {
    position: absolute;
    top: 5px;
    left: 5px;
    background-color: #9999d9;
    color: white;
    padding: 5px;
    border-radius: 0 15px 15px 15px;
    font-family: Comfortaa, serif;
}
.not_found_page h2 {
    margin: 10px auto;
    background-color: white;
    max-width: max-content;
    padding: 10px;
    border-radius: 10px;
}